html { box-sizing: border-box; }
*, *:before, *:after { box-sizing: inherit; }
img { box-sizing: content-box; }
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
* {
	outline: 0 none;
}
a{
	color: #fff !important;
	text-decoration: none;
	font-weight: 700;
}
header{
	& + section{
		&:not(.banner){
			padding-top: calc(80px + 64px);
		}
		&:last-of-type{
			padding-bottom: 64px;
		}
	}
}
body {
	line-height: 1.2;
	background: #0A0A25;
	color: #fff !important;
    overflow-x: hidden;
    max-width: 100vw;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
	font-family: "Roboto", sans-serif;
	&.hidden{
		overflow: hidden;
	}
	&.admin-bar{ 
		&.home{
			header{
				& + section{
					padding-top: 134px;
				} 
			}
		}
		header{
			top: 32px;	
		}
	} 
	&.home{
		header{
			& + section{
				padding-top: 80px;
			} 
		}
	}
}
section{
	&:not(.banner){
		padding-top: 64px;
	}
	& + footer{
		margin-top: 64px;
	}
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
li{
	&.btn{
		a{
			display: flex;
			flex-direction: row;
			align-items: center;
			flex-wrap: wrap;
			justify-content: center;
			gap: 10px;
			height: 40px;
			border-radius: 6px;
			padding: 0 24px;
			max-width: fit-content;
			margin-left: auto;
			margin-right: auto;
			transition: all 0.3s ease;
			background: #2375E5;
			
				
		}
		@media screen and (min-width: 1025px){
			&:hover{
				&:before{
					color: var(--blue) ;
				}
				a{
					background: #fff;
					color: var(--blue) !important;
				}
			}
		}
	}
}
.primary-cta,
button.btn,
input.btn,
a.btn{
	display: flex;
	flex-direction: row;
	align-items: center;
	flex-wrap: wrap;
	justify-content: center;
	gap: 10px;
	height: 40px;
	border-radius: 6px;
	padding: 0 24px;
	max-width: fit-content;
	margin-left: auto;
	margin-right: auto;
	transition: all 0.3s ease;
	background: #2375E5;
	@media screen and (min-width: 1025px){
		&:hover{
			background: #fff;
			color: #2375E5  !important;
		} 
	}
}
